	module.exports = {"data":{"wordpressPost":{"title":"Katy Perry Launched A New Fragrance And Excuse Me But It&#8217;s #AWESOME","content":"<p>I don&#8217;t know about you, but I tend to think of celebrity scents as well, not quite as legitimate as those that come from design houses. But the truth is, they&#8217;ve every bit as legit, occasionally even more so.</p>\n<p>The truth of the matter is that celebrities employ the world&#8217;s best noses to create their scents and those noses (as professional fragrance-makers are known) are the very same ones every A-List fragrance house tap too. So, now we have that on the table, let&#8217;s talk <strong>Katy Perry&#8217;s Indi</strong>.</p>\n<p>In a nutshell &#8211; you want it. And we&#8217;ll let the woman herself explain exactly why.</p>\n<figure id=\"attachment_3247\" style=\"width: 176px\" class=\"wp-caption alignnone\"><img class=\"size-medium wp-image-3247\" src=\"http://the-beautyinsider.com/wp-content/uploads/2017/09/Katy_Perry_INDI_EDP_100ml_BTL-176x300.jpg\" alt=\"\" width=\"176\" height=\"300\" srcset=\"http://the-beautyinsider.com/wp-content/uploads/2017/09/Katy_Perry_INDI_EDP_100ml_BTL-176x300.jpg 176w, http://the-beautyinsider.com/wp-content/uploads/2017/09/Katy_Perry_INDI_EDP_100ml_BTL-768x1307.jpg 768w, http://the-beautyinsider.com/wp-content/uploads/2017/09/Katy_Perry_INDI_EDP_100ml_BTL-602x1024.jpg 602w, http://the-beautyinsider.com/wp-content/uploads/2017/09/Katy_Perry_INDI_EDP_100ml_BTL.jpg 1350w\" sizes=\"(max-width: 176px) 100vw, 176px\" /><figcaption class=\"wp-caption-text\">Fabulous scent and sophisticated packaging make this one a winner.</figcaption></figure>\n<p>&nbsp;</p>\n<p>“With INDI, I finally got to play with a bold musk scent. It&#8217;s a combination of feminine and masculine, and it&#8217;s sexy without being too in your face. It&#8217;s about expressing your true self and enhancing the incredible individual you are, ” says Katy.</p>\n<p>What we&#8217;re talking about here is a one of a kind seductive musk fragrance that is subtle yet genuinely noticeable &#8211; and in a really excellent way.</p>\n<p>The fragrance is made up of 11 different musks that blend beautifully with top notes of oriental plum and Italian bergamot. The combination of fresh muguet and white cedar wood mingle with the velvety warmth of amber and vanilla in the background.</p>\n<p>For those who like all of the details the breakdown is as follows:</p>\n<p><strong>Top Notes</strong>: White Tea, Oriental Plum, Italian Bergamot<br />\n<strong>Heart Notes</strong>: Fresh Muguet, Cyclamen, White Cedar Wood<br />\n<strong>Base Notes</strong>: Amber Sultan, Vanilla Tonka, Seductive Musks</p>\n<p><strong>The Perfumer</strong>: Caroline Sabas</p>\n<p><strong>The Fragrance House</strong>: Givaudan</p>\n<p>And because packaging matters, the fragrance is showcased in a sleek sophisticated opal glass. Two sides of the modern bottle contain a special embossed message of empowerment from Katy Perry, Put the U in Individual. The finishing touch is the matte black cap featuring an embossed KPINDI.</p>\n<p><strong>INDI EDP 30ml: $39, EDP 50ml: $49, EDP 100ml: $69.</strong></p>\n<p>Available at Priceline, Chemist Warehouse and Independent Pharmacies nationally.</p>\n","better_featured_image":{"source_url":"http://the-beautyinsider.com/wp-content/uploads/2017/09/Katy_Perry_INDI_EDP_100ml_PKSHT.jpg"},"categories":[{"slug":"lifestyle","name":"Lifestyle"}]},"site":{"siteMetadata":{"title":"The Beauty Insider"}}},"pathContext":{"id":"88a1b961-bce9-59f4-8a31-e5336c931faf"}}

